      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q2" v-if="showQ(2)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q3" v-if="showQ(3)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q4" v-if="showQ(4)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(4)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q5" v-if="showQ(5)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q6" v-if="showQ(6)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q7" v-if="showQ(7)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q8" v-if="showQ(8)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q9" v-if="showQ(9)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <ul class="questions" id="q10" v-if="showQ(10)">
      <li><button v-on:click="selectAnswer(1)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(2)" class="button"></button></li>
      <li><button v-on:click="selectAnswer(3)" class="button"></button></li>
    </ul>
    <Tunnel ref="tunnel" :index="this.index" :score="this.score" v-on:next="resetQuestionIndex"/>
  </div>
</template>

<script>
import Tunnel from '@/components/Tunnel.vue'
import { bus } from '../main'

export default {
  name: 'Qa',
  props: ['index'],
  components: {
    Tunnel
  },
  data() {
    return {
      qa: null,
      background: null,
      answersActive: true,
      result: true,
      score: null,
      questionIndex: 0
    }
  },
  created() {
    fetch('./data/qa.json')
    .then(r => r.json())
    .then(json => {    
      this.qa = json.qa
      this.setBackground(0)
    })

    // Notifie Menu.vue
    bus.$emit('display-home-button', true)

    // Rinit le score en cas de nouvelle partie
    this.score = 0

    // Update background (w/ lang)
    bus.$on('lang-updated', () => {
      this.setBackground(this.questionIndex)
    })
  },
  watch: {
    $route() {
      this.setBackground(0)
      this.answersActive = true
      this.$refs.tunnel.showNextButton = false
    }
  },
  methods: {
    showQ(target) {
      if(target == this.index)
        return true
      else
        return false
    },
    setBackground(index) {
      this.background = require('@/assets/img/'+this.$lang+'/'+this.qa[this.index].img[index]+'.png')
    },
    selectAnswer(index) {
      this.questionIndex = index
      if(this.answersActive) {      
        this.result = this.qa[this.index].answers[index-1]
        // Dsactive les buttons rponse une fois qu'on a rpondu
        this.answersActive = false
        // Update Tunnel
        this.$refs.tunnel.updateDot(this.index, this.result)
        // Update score
        if(this.result == true){
          this.setBackground(1)
          this.score ++
        }
        else {
          this.setBackground(2)
        }
      }
    },
    resetQuestionIndex() {
      this.questionIndex = 0
    }
  }
}
</script>

