<template>
  <div id="menu">
      <a href="#" v-on:click="updateLang('fr', $event)"><img src="@/assets/img/ui/btn-fr.png"></a>
      <a href="#" v-on:click="updateLang('uk', $event)"><img src="@/assets/img/ui/btn-uk.png"></a>
      <br>
      <router-link to="/"><img src="@/assets/img/ui/btn-home.png" v-if="homeButtonVisible"></router-link>
    </div>
</template>

<script>
import Vue from 'vue'
import { bus } from '../main'

export default {
  name: 'Menu',
  data() {
    return {
      homeButtonVisible: false
    }
  },
  created() {
    bus.$on('display-home-button', (visible) => {
      this.homeButtonVisible = visible
    })
  },
  methods: {
    updateLang(lang, e) {
      e.preventDefault()
      Vue.prototype.$lang = lang
      bus.$emit('lang-updated')
    }
  }
}
</script>

<style lang="scss" scoped>
#menu{
  position: absolute;
  top: 350px;
  left: 0;
  z-index: 1;

  a{
    display: block;
  }
}
</style>