<template>
  <div id="home" :style="{ 'background-image': 'url(' + homeBg + ')' }">
    <div id="sleep" v-if="sleep" :style="{ 'background-image': 'url(' + sleepBg + ')' }">
      <button id="btn-sleep" v-on:click="wakeUp"></button>
    </div>
    <router-link  v-if="!sleep" to="/qa/0" class="button"></router-link>
  </div>
</template>

<script>
import Vue from 'vue'
import { bus } from '../main'

export default {
  name: 'Home',
  data() {
    return {
      sleep: null,
      sleepBg: null,
      homeBg: null
    }
  },
  created() {
    this.sleep = this.$sleep
    //
    bus.$emit('display-home-button', false)
    //
    this.sleepBg = require('@/assets/img/'+this.$lang+'/sleep.png')
    this.homeBg = require('@/assets/img/'+this.$lang+'/home.png')
    bus.$on('lang-updated', () => {
      this.sleepBg = require('@/assets/img/'+this.$lang+'/sleep.png')
      this.homeBg = require('@/assets/img/'+this.$lang+'/home.png')
    })
  },
  methods: {
    wakeUp() {
      this.sleep = Vue.prototype.$sleep = false
    }
  }
}
</script>

<style scoped>
#sleep{
  width: 100%;
  height: 100%;
}
#btn-sleep{
  width: 100%;
  height: 100%;
  opacity: 0;
}
#home{
  width: 100%;
  height: 100%;
}
.button {
  width: 820px;
  height: 140px;
  display: block;
  background-color: rosybrown;
  opacity: 0;
  position: absolute;
  left: 0; right: 0; bottom: 280px;
  margin: auto;
}
</style>