<template>
  <div id="tunnel">
    <ul>
      <li v-for="(dot, index) in dots" :key="index">
        <img :src="require('@/assets/img/ui/' + dot)" alt="dot">
      </li>
    </ul>
    <router-link :to="nextIndex" id="btn-next" v-if="showNextButton" v-on:click.native="next">
      <img src="@/assets/img/ui/btn-next.png" alt="Bouton suivant">
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Tunnel',
  props: ['index', 'score'],
  data() {
    return {
      maxQuestions: 11,
      showNextButton: false,
      dots: [
        'pastille01.png',
        'pastille02.png',
        'pastille03.png',
        'pastille04.png',
        'pastille05.png',
        'pastille06.png',
        'pastille07.png',
        'pastille08.png',
        'pastille09.png',
        'pastille10.png',
        'pastille11.png'
      ]
    }
  },
  computed: {
    // Incrmente l'index (renvoy au composant "Qa" via $router)
    nextIndex() {
      if(this.index < this.maxQuestions - 1)
        return '/qa/' + (parseInt(this.index) + 1)
      else
        return '/result/' + this.score
    }
  },
  methods: {
    updateDot(dotIndex, result) {
      if(dotIndex < this.maxQuestions){
        if(result == true)
        this.dots.splice(dotIndex, 1, 'pastille-green.png')
      else
        this.dots.splice(dotIndex, 1, 'pastille-red.png')
      }  

      this.showNextButton = true
    },
    next() {
      // Next pour QA (reset questionIndex)
      this.$emit('next')
    }
  }
}
</script>

