import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Qa from '../views/Qa.vue'
import Result from '../views/Result.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/qa/:index',
    component: Qa,
    props: true
  },
  {
    path: '/result/:score',
    component: Result,
    props: true
  }
]

const router = new VueRouter({
  routes
})

export default router
