<template>
  <div id="app">
    <Menu/>
    <transition name="slide-fade">
      <router-view/>
    </transition>   
  </div>
</template>

<script>
import Menu from '@/components/Menu.vue'

export default {
  name: 'App',
  data() {
    return {
      timer: null,
      timeLeft: 120000
    }
  },
  components: {
    Menu
  },
  created(){
    window.onclick = this.resetTimer
  },
  methods: {
    displaySleepScreen() {
      if(this.$router.currentRoute.name == 'Home')
        this.$router.go()
      else {
        this.$router.push({path: '/'}) 
        this.$router.go()
      }
        
    },
    resetTimer() {
      clearTimeout(this.timer)
      this.timer = setTimeout(this.displaySleepScreen, this.timeLeft)
    }
  }
}
</script>

